export interface IPerson {
	_id?: string;
	first_name: string;
	second_name: string;
	last_name: string;
	gender: string;
	birthdate: Date;
	phone: number;
    email: string;
    picture: any;
}

export class Person implements IPerson {
    public _id: string;
	public first_name: string;
	public second_name: string;
	public last_name: string;
	public gender: string;
	public birthdate: Date;
	public phone: number;
    public email: string;
    public picture: any;
	
	constructor(object?: any){
		if(object != undefined) {
			this.setParams(object);
		}
	}

    setParams(object: any) : void {
        this._id = object._id || this._id;
        this.first_name = object.first_name || this.first_name;
        this.second_name = object.second_name || this.second_name;
        this.last_name = object.last_name || this.last_name;
        this.gender = object.gender || this.gender;
        this.birthdate = object.birthdate ? this.parseDate(object.birthdate) : this.birthdate;
        this.phone = object.phone || this.phone;
        this.email = object.email || this.email;
        this.picture = object.picture || this.picture;
    }

    default() : void {
        this._id = null;
		this.first_name = null;
        this.second_name = null;
        this.last_name = null;
        this.gender = 'female';
        this.birthdate = null;
        this.phone = null;
        this.email = null;
        this.picture = null;
    }

    clear() : void {
		this._id = null;
		this.first_name = null;
        this.second_name = null;
        this.last_name = null;
        this.gender = null;
        this.birthdate = null;
        this.phone = null;
        this.email = null;
        this.picture = null;
	}

    private parseDate(date) {
        if(typeof date == 'string') {
            if(date.search(/\d{2}.\d{2}.\d{4}/i) == -1) return date;
            else {
                let [day, month, year] = date.split('.');
                return new Date(parseInt(year), parseInt(month) - 1, parseInt(day), 0, 0, 0);
            }
        } else {
            return date;
        }
    }
}



